.header {
    padding: 70px;
    text-align: center;
    background: linear-gradient(
        rgba(255, 255, 255, 0),
        rgba(255, 255, 255, 0)
      ),
      url("../covidtracker.jpg");
    color: white;
    text-shadow: 4px 4px black,-4px 4px black,4px -4px black,-4px -4px black;
  }
  
  /* Increase the font size of the h1 element */
  .header h1 {
    font-size: 50px;
  }
  .main-body{
    /* display: flex; */
    width: 100%;
  }
#selectcnt{
    text-align: center;
    margin: 5px 0px 5px 0px;
}
#selectcnt button{
    background-color: rgb(28, 104, 219);
    color: white;
    border: 2px solid black;
    font-weight: 900;
    font-size: 17px;
    margin-left: 2px;
}
#selectcnt select{
  min-width:55%;
}
#cntry{
  font-size: 18px;
}
#Table{
  width:60%;
  margin: 2px auto 2px auto;
}
#extras{
  width: 60%;
  margin: 0px auto 2px auto;
}
#extras select{
  width: 49%;
  font-size: 15px;
  font-weight: 800;
  background-image: linear-gradient(blue, black);
  color: antiquewhite;
  border: 2px solid black;
  margin: 0.5%;
}
#extras select option{
  color: black;
}
th{
  border-top: 2px solid black;
  border-bottom: 2px solid black;
}
#topbtn{
  color: white;
  background-color: black;
  font-size: 15px;
  font-weight: 900;
  border-radius: 10px;
  position: fixed;
  bottom: 10px;
  right: 5%;
}
@keyframes glow {
  from {
    background-color: #504dff;  
  }
  to {
    background-color: unset;
  }
}